.space{
  padding-top: 50px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.footer{
  background-color: #dfe8fc;
  margin:0px auto;
  padding: 20px 0px 20px 0px;
}

.pad{
  padding-top:500px;
}

.im-sz{
 height:200px;
 width:200px;
}
.section{
  height: 315px;
  background-color: #dfe8fc;
}

.phont{
  font-family: 'Capriola', sans-serif;
}

.nb{
  font-family: 'Varela Round', sans-serif;
}

.ph{
  font-family: 'Cabin', sans-serif;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Roboto', sans-serif;
  font-family: 'Varela Round', sans-serif;
}

.navbar,
.navbar-default {
  align-items: center;
  background: #33518E;
  color: white;
  font-weight: 300;
  /*background-image: linear-gradient(to bottom,#4361ee,transparent 90%) !important; !* override background image gradient w/ flat color *!*/
}
.navbar__title {
  margin-right: auto;
  font-size: 150%;
  padding: 12px 16px;
}

.navbar__item {
  padding: 16px 16px;
  cursor: pointer;
  vertical-align: middle;
}

